#header{
  width:100%;
  height:350px;
  margin-bottom:-50px;
  background-color:#00E676;
  background-image:url(/static/media/header-bg.00a589ab.png);
  background-size:100% auto;
  color:white
}

.reverse-button{
  background:#00E676;
  cursor:pointer;
  position:absolute;
  right:0;
  left:0;
  margin:auto;
  border-radius:30px;
  top:0;
  bottom:0;
  width:120px;
  height:120px;
  color:white;
  box-shadow:0 0 20px rgba(0,179,92,0.6);
  padding:35px;
  text-align:center;
  font-size:14px
}

.reverse-button:hover{
  background:#00dc71
}

.copy-button{
  position:absolute;
  width:35px;
  height:35px;
  overflow:hidden;
  left:-35px;
  border-radius:10px 0 0 10px;
  top:0;
  bottom:0;
  margin:auto;
  border:1px solid #00E676;
  border-right:none;
  line-height:35px;
  vertical-align:middle;
  color:#00E676;
  cursor:pointer
}

.copy-button:hover{
  background:#00E676;
  color:white
}

.textarea{
  padding:20px;
  border:1px solid #C9C9C9;
  border-radius:30px;
  resize:none;
  width:100%;
  display:block;
  color:#444;
  min-height:250px
}

.textarea::-webkit-input-placeholder{
  color:#C9C9C9
}

.textarea:-ms-input-placeholder{
  color:#C9C9C9
}

.textarea::placeholder{
  color:#C9C9C9
}

.textarea:focus{
  border-color:#00E676;
  outline:none
}

